export default class AppModel {
  constructor(frames, fps) {
    this.frames = frames;
    this.fps = fps;
  }

  async createAnimation() {
    let ol = -1;
    const storage = this.frames;
    function animate(o) {
      if (storage[o]) {
        for (let i = 0; i < 100; i += 1) {
          const animation = document.querySelectorAll('.animation')[0];
          const pix = animation.querySelectorAll('.small-pix');
          pix[i].style.backgroundColor = storage[o][i];
        }
      }
    }
    AppModel.int = setInterval(() => {
      if (ol < this.frames.length - 1) {
        ol += 1;
      } else if (ol >= this.frames.length - 1) {
        ol = 0;
      }
      animate(ol);
    }, (this.fps * 100));
  }

  async stopAnimation() {
    this.fps = document.querySelector('input').value;
    clearInterval(AppModel.int);
  }
}
