import AppView from '../views/index';
import AppModel from '../models/AppModel';

export default class App {
  constructor() {
    this.square = Array(100).fill(0);
    this.frameCount = 0;
    this.selectedFrame = 0;
    this.framesStorage = [];
    this.currentColor = 'black';
    this.fps = 1;
  }

  async start() {
    const view = new AppView();
    const model = new AppModel(this.framesStorage, this.fps);
    await view.init();
    await view.createFrame(this.frameCount);
    this.eventListeners();
    model.createAnimation();
  }

  async eventListeners() {
    const view = new AppView();
    this.selected = document.querySelector('.frame');
    this.selected.classList.add('selected');
    const workspace = document.querySelector('.workspace');
    const framesBlock = document.querySelector('.frames-block');
    const menu = document.querySelector('.menu');
    const range = document.querySelector('input');
    workspace.addEventListener('mouseover', (e) => {
      e.target.style.opacity = '0.1';
    });
    workspace.addEventListener('click', (e) => {
      if (e.target.tagName === 'DIV' && e.target.className === 'pixel') {
        e.target.style.backgroundColor = this.currentColor;
      }
      this.autoSave();
    });
    workspace.addEventListener('mouseout', (e) => {
      e.target.style.opacity = '1';
    });
    framesBlock.addEventListener('click', (e) => {
      if (e.target.tagName === 'LI') {
        this.selectFrame(e.target, e.clientY);
      } else if (e.target.parentNode.className === 'frame') {
        this.selectFrame(e.target.parentNode, e.clientY);
      }
    });
    menu.addEventListener('click', (e) => {
      if (e.target.tagName === 'LI') {
        document.querySelectorAll('.menu-color-picker')[0].style.backgroundColor = e.target.style.backgroundColor;
        this.currentColor = e.target.style.backgroundColor;
      }
    });
    framesBlock.addEventListener('click', (e) => {
      if (e.target.className === 'create-frame' || e.target.innerHTML === 'create') {
        this.frameCount += 1;
        view.createFrame(this.frameCount);
      } else if (e.target.className === 'clone-frame' || e.target.innerHTML === 'clone') {
        const cloneFrame = view.cloneFrame(this.selectedFrame, this.framesStorage, this.frameCount);
        this.frameCount += 1;
        for (let i = 0; i < cloneFrame.length; i += 1) {
          for (let j = 0; j < cloneFrame[i].length; j += 1) {
            this.framesStorage[i][j] = cloneFrame[i][j];
          }
        }
      }
    });
    range.addEventListener('click', () => {
      this.fps = document.querySelector('input').value;
      const model = new AppModel(this.framesStorage, this.fps);
      model.stopAnimation();
      model.createAnimation();
    });
  }

  async selectFrame(target, clientY) {
    if (this.selected) {
      this.selected.classList.remove('selected');
      this.selectedFrame = Math.round((clientY) / 80) - 1;
    }
    this.selected = target;
    this.selected.classList.add('selected');
    this.square.forEach((e, i) => {
      const pixel = document.getElementsByClassName('pixel')[i];
      if (this.framesStorage[this.selectedFrame]) {
        pixel.style.backgroundColor = this.framesStorage[this.selectedFrame][i];
      } else {
        pixel.style.backgroundColor = 'darkcyan';
      }
    });
  }

  async autoSave() {
    this.arrFr = [];
    const currentFramePix = document.querySelectorAll('.frame')[this.selectedFrame].querySelectorAll('.small-pix');
    for (let i = 0; i < 100; i += 1) {
      const pixel = document.getElementsByClassName('pixel')[i];
      this.arrFr[i] = (getComputedStyle(pixel).backgroundColor);
      currentFramePix[i].style.backgroundColor = this.arrFr[i];
    }
    this.framesStorage[this.selectedFrame] = (this.arrFr);
  }
}
