export default class AppView {
  constructor() {
    this.square = Array(100).fill(0);
    this.buttons = ['pnt bckt', ' ', 'move', 'trnsfrm'];
    this.initColors = ['black', 'yellow', 'green', 'pink', 'black'];
    this.cloneInfo = [];
  }

  static createEl(tagname, classname, parent, num) {
    let n;
    if (!num) {
      n = 0;
    } else n = num;
    const el = document.createElement(`${tagname}`);
    el.className = `${classname}`;
    const parentEl = document.querySelectorAll(`${parent}`)[n];
    parentEl.appendChild(el);
  }

  static menu(buttons, colors) {
    buttons.forEach((e, i) => {
      AppView.createEl('ul', 'menu-button', '.menu');
      document.getElementsByClassName('menu-button')[i].innerHTML = e;
    });
    colors.forEach((e, i) => {
      AppView.createEl('li', 'menu-color-picker', '.menu-button', 1);
      document.getElementsByClassName('menu-color-picker')[i].style.backgroundColor = e;
    });
  }

  async createFrame(framesCount) {
    AppView.createEl('li', 'frame', '.frames-block');
    this.square.forEach(() => {
      const frame = document.querySelectorAll('.frame')[framesCount];
      const pix = document.createElement('div');
      pix.className = 'small-pix';
      pix.style.backgroundColor = 'rgb(0, 139, 139)';
      frame.appendChild(pix);
      AppView.createEl('div', 'small-pix', '.animation');
    });
  }

  async init() {
    AppView.createEl('div', 'main', 'body');
    AppView.createEl('div', 'menu', '.main');
    AppView.createEl('ul', 'frames-block', '.main');
    AppView.createEl('ul', 'create-frame', '.frames-block');
    document.querySelector('.create-frame').innerHTML = '<p>create</p>';
    AppView.createEl('ul', 'clone-frame', '.frames-block');
    document.querySelector('.clone-frame').innerHTML = '<p>clone</p>';
    AppView.createEl('div', 'workspace', '.main');
    this.square.forEach(() => {
      AppView.createEl('div', 'pixel', '.workspace');
    });
    AppView.createEl('div', 'animation', '.main');
    AppView.createEl('input', 'frame-range', '.main');
    const range = document.querySelectorAll('input')[0];
    range.setAttribute('orient', 'vertical');
    range.setAttribute('type', 'range');
    range.setAttribute('step', '1');
    range.setAttribute('min', '1');
    range.setAttribute('max', '10');
    range.setAttribute('value', '1');
    await AppView.menu(this.buttons, this.initColors);
  }

  async cloneFrame(currFrame, storage, count) {
    // create
    this.createFrame(count + 1);
    // fill
    storage.push([]);
    const pix = document.querySelectorAll('.frame')[count + 1].childNodes;
    this.square.forEach((e, i) => {
      if (storage[currFrame][i]) {
        pix[i].style.backgroundColor = storage[currFrame][i];
        storage[currFrame + 1].push(storage[currFrame][i]);
      } else {
        pix[i].style.backgroundColor = 'darkcyan';
        storage[currFrame].push(pix[i].style.backgroundColor);
      }
    });
    return storage;
  }
}
